<template>
    <div>
  <section class="page-action">
    <div class="container-fluid px-0">
        <div class="row">
            <input type="hidden" value="@titleName" id="hdnModuleName" />
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition partition-full p-actions-expand before-d-none overflow-visible">
                    <span class="p-name text-white">{{($t('Product') + " " +$t('dashboard'))}}</span>
                    <span class="p-actions  float-right">
                      <a class="p-action-btn text-white d-none" id="ancChangeView" data-isshow="0"><i class="fa fa-pencil" aria-hidden="true"></i><br>Change View</a>
                      <span class="p-actions float-right">
                            <date-range pgaction="global" @set-date="SetDateRange"></date-range>                              
                            </span>
                    </span>
                    <span class="collapse-head-btns">
                        <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                    </span>
                </div>
            </div>
        </div>
    </div>
    <input type="hidden" value="dashboard" id="hdnpagename" />
</section>
<section class="main-content nd-combo-view">
    <div id="parentPageDiv" class="container-fluid padding-t_10 px-0">
        <div class="col-md-12 p-0">
            <!--dashboard-->
          
            <!--dashboard-->
            <div class="clearfix"></div>
            <div class="col-lg-12 p-0">
                <div class="row mt-2">    
                        
                        <div class="dashbord-graph-widget row bg-lgray border m-0 py-3 with-combo scrollbarmini" id="Reports">                       
                            <widget-report v-if="widgets && widgets.length>0 && fromDate" ref="widget" :widgets="widgets" :moduleName="moduleName" :viewType="viewType"
                                :fromDate="fromDate" :toDate="toDate" @reload-report="GetReports"></widget-report>
                        </div>
                        <div class="clearfix"></div>
                   
                    <div class="col-md-12 mt-3" id="divAddMoreReports">
                        <div class="text-right mt-1">
                            <a href="javascript:;" id="hlkAddMoreReports" class="btn btn-success" @click="OnAddMore">
                                <i class="fa fa-plus pr-2"></i>{{$t('AddMore')}}</a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-xl-12 p-0" style="display:none;" id="divToDoListMain">    
            </div>
        </div>   
    </div>
   
    <div class="my-popups view-popup">
        <!-- The Modal -->
        <div class="modal fade" id="myModal">
            <div class="modal-dialog modal-lg modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">Change View</h4>
                        <button type="button" class="close" data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <!-- Modal body -->
                    <div class="modal-body p-0">
                        <div class="row m-0">
                            <div class="col-sm-12 col-md-12 col-lg-7 col-xl-7 float-left d-none">
                                <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                    <div class="form-group m-0">
                                        <input id="search" class="form-control" type="text" placeholder="Search" autocomplete="off">
                                    </div>
                                </div>
                                <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                    <div class="search-btm-btn background-0 text-left form-check-inline">
                                        <a title="" id="Go" href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a>
                                        <a href="javascript:;" title="" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear"><i class="fa fa-refresh"></i></a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-5 col-xl-5 float-left d-none">
                                <div class="float-right">
                                    <a href="javascript:;" class="btn btn-primary"> <i class="fa fa-plus pr-2" aria-hidden="true"></i>Add</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</div>
</template>

<script>
import DataService from "../../../service/DataService"
import WidgetReport from './WidgetReport.vue';
import DateRange from './DateRange.vue';
export default {
  components: { WidgetReport, DateRange},
    data() {
   return {
    titleName:'',
    moduleName:'Asset',
    widgets:[],
    viewType:'R',
    fromDate:null,
    toDate:null,
    MenuButton:false,
    toDos:[],
    encCompanyId:null,
    encUserId:null,
    encTrackId:null,
    fromDt:null,
    toDt:null
   }
},
created:function(){
    debugger
    this.ClickTimer('Month',null,'global')
    this.GetReports();
},
mounted(){
            $("#divReportsdata, #divAddMoreReports").show();
            $("#divReportsdata").addClass("col-md-12").removeClass("col-md-7").show();
            $("#divReportsdata").find(".graph-widget").addClass("col-md-4").removeClass("col-md-6");   
},
methods:{
    OnAddMore(){
   this.$refs.widget.$data.showReports=true;
    },
    FetchCountListData(type){
        this.$refs.countList.$data.FetchData(type,this.$refs.countList.$data.users);
    },
    GetStartEndDates(dateSpan, e, action) {
            debugger
            var vm = this;
            vm.isLoading = true;
            var params = `DateSpan=${dateSpan.toLowerCase()}&report=${$('#hdnGroupBy').val()}`;
            DataService.GetStartEndDate_New(params).then(function (response) {
                vm.isLoading = false;
                var data = response.data;
                vm.fromDate = data.dateStart;
                vm.toDate = data.dateEnd;
                var fdt=data.dateStart;
                var tdt=data.dateEnd;
                $('#txtFromDate_' + action).val(data.dateStart);
                $('#txtToDate_' + action).val(data.dateEnd);
                $('#aDateLable_' + action).find('em').text(data.displayText);
                vm.$emit('set-date', data.dateStart, data.dateEnd);
                if (action != 'global') {
                    setTimeout(() => {

                        vm.FetchDataSingle(action, fdt, tdt);
                    }, 500);
                }


            });


        },
    ClickTimer(type, e, action) {
            debugger
            var vm = this;
            $("#hdnViewId").val($(e).parent().attr("id"));
            if ($("#hdnViewId").val() == $("#hdnPinnedViewId").val()) {
                //$("#idPinView").css("color", "orange");
                $("#idPinView").closest(".btnPin").addClass("pinmyview");
                $("#idPinView").closest(".btnPin").removeClass("unpinmyview");
            }
            else {
                //$("#idPinView").css("color", "white");            
                $("#idPinView").closest(".btnPin").addClass("unpinmyview");
                $("#idPinView").closest(".btnPin").removeClass("pinmyview");
            }
            $(".btnPin").show();
            /*alert($("#hdnViewId").val());*/
            if ($(e).closest("div.DatePopUp").attr("data-pageaction") == "global") {
                $(e).closest('.date_range').find('#aDateLable_global').find('em.text-white').html('').html($(e).text());
                vm.isLoading = true;
                //vm.FadeInOut1(e);
                $("#dashbord-graph-widget").find(".ticketingDashboardHead").each(function () {
                    ////debugger;
                    $(this).find(".DatePopUp").find("a[class=" + type + "]").trigger("click");
                    $(this).find(".DatePopUp").hide();
                });
                if (type == "Today") {
                    vm.GetStartEndDates(type, e, action);
                }
                else if (type == "Yesterday") {
                    vm.GetStartEndDates("yesterday", e, action);
                }
                else if (type == "Week") {
                    vm.GetStartEndDates("thisweek", e, action);
                }
                else if (type == "LastWeek") {
                    vm.GetStartEndDates("lastweek", e, action);
                }
                else if (type == "Month") {
                    vm.GetStartEndDates("thismonth", e, action);
                }
                else if (type == "LastMonth") {
                    vm.GetStartEndDates("lastmonth", e, action);
                }
                else if (type == "AllTime") {
                    vm.GetStartEndDates("thisyear", e, action);
                }
                vm.isLoading = false;
            }
            else {
                if (type == "Today") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates(type, e, action);
                }
                else if (type == "Yesterday") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates("yesterday", e, action);
                }
                else if (type == "Week") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("week");
                    vm.GetStartEndDates("thisweek", e, action);
                }
                else if (type == "LastWeek") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("week");
                    vm.GetStartEndDates("lastweek", e, action);
                }
                else if (type == "Month") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("month");
                    vm.GetStartEndDates("thismonth", e, action);
                }
                else if (type == "LastMonth") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("month");
                    vm.GetStartEndDates("lastmonth", e, action);
                }
                else if (type == "AllTime") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates("thisyear", e, action);
                }
               //vm.FadeInOut1(e);
            }
            $(e).closest('.date_range').find('#imgNext').show();
            $(e).closest('.date_range').find('#imgPrev').show();
        },
    GetToDoList(){
        var vm = this;
        DataService.gettoDoList('ModuleName=' + vm.moduleName)
            .then(function (response) {
                console.log(response);
                vm.encCompanyId=response.data.COMPANY_ID;
                vm.encUserId=response.data.CREATED_BY;
                vm.encTrackId=response.data.TRACK_ID;
                //vm.toDos = response.data.list;
            })
    },
    GetReports(){
        debugger
        var vm = this;
        DataService.GetReports('moduleName=' + vm.moduleName)
            .then(function (response) {               
             vm.widgets=response.data.list;
             //vm.GetToDoList();
            })
    },
    SetDateRange(from, to) {   
        debugger    
        this.fromDate = from;
        this.toDate = to;
        this.GetReports();
    },
   
}
}
</script>